<template>
  <section class="container">
    <div v-if="loading">
      <h3>...Loading</h3>
    </div>
    <ul v-if="artists">
      <router-link to="/newArtist" class="btn">Add New Artist</router-link>
      <li :key="artist.id" v-for="artist in artists">
        <router-link :to="`/artist/${artist.artist_id}`">
          <v-img
            :src="artist.profilePic"
            lazy-src="https://picsum.photos/id/11/10/6"
            aspect-ratio="1"
            contain
          ></v-img>
        </router-link>
      </li>
    </ul>
  </section>
</template>

<script>
import { db } from '../main.js';
export default {
  name: 'Home',
  data: () => ({
    artists: [],
    loading: false
  }),
  beforeCreated() {
    this.loading = true;
  },
  created() {
    this.getArtists();
  },
  methods: {
    async getArtists() {
      let snapshot = await db.collection('artists').get();

      let artists = [];
      snapshot.forEach(doc => {
        let appData = doc.data();
        appData.id = doc.id;
        artists.push(appData);
      });

      this.artists = artists;
      this.loading = false;
    }
  }
};
</script>

