<template>
  <section>
    <div class="container">
      <div class="grid">
        <form v-on:submit.prevent="onSubmit">
          <div class="form-group">
            <!-- <label for="name">Full Name</label> -->
            <!-- <input type="text" id="name" name="name" placeholder="Add New Artist" v-model="name" /> -->
            <v-text-field label="Enter Your Name..." v-model="artistName" outlined></v-text-field>
          </div>
          <div class="form-group">
            <!-- <label for="artistProfilePic">Upload Profile Image</label> -->
            <v-file-input v-model="artistProfilePic" label="Choose Profile Picture..."></v-file-input>
          </div>
          <div class="form-group">
            <input type="submit" value="Add New Artist" class="btn" />
          </div>
        </form>
      </div>
    </div>
    <router-link to="/">Go Back</router-link>
  </section>
</template>

<script>
import { db } from '../main.js';
import firebase from 'firebase';

export default {
  name: 'NewArtist',
  data: () => ({
    artistName: null,
    artistProfilePic: null,
    artistId: null
  }),
  methods: {
    onSubmit() {
      const ref = firebase.storage().ref();

      var metadata = {
        contentType: 'image/jpeg'
      };

      this.artistId = this.artistName
        .split(' ')
        .join('')
        .toLowerCase();

      // Upload profile image to Storage
      const uploadTask = ref
        .child(`${this.artistId}-profile.jpg`)
        .put(this.artistProfilePic, metadata)
        .then(snapshot => {
          snapshot.ref.getDownloadURL().then(downloadURL => {
            // Add new Artist to Firestore
            db.collection('artists')
              .add({
                artist_id: this.artistId,
                name: this.artistName,
                profilePic: downloadURL
              })
              .then(docRef => {
                this.$router.push('/');
              });
          });
        });
    }
  }
};
</script>

