<template>
  <div>
    <section class="header">
      <h3>This is {{this.artist_name}} - The #1 DJ in Sweden</h3>
      <!-- <div class="artist"></div> -->
      <v-img
        :src="artist_profile_pic"
        lazy-src="https://picsum.photos/id/11/10/6"
        aspect-ratio="1.7778"
        class="grey lighten-2"
        max-width="600"
        max-height="400"
      ></v-img>
    </section>
    <router-link to="/" class="btn">Go Back</router-link>
  </div>
</template>

<script>
import { db } from '../main.js';

export default {
  name: 'Artist',
  data: () => ({
    artist_id: null,
    artist_name: null,
    artist_profile_pic: ''
  }),
  beforeRouteEnter(to, from, next) {
    db.collection('artists')
      .where('artist_id', '==', to.params.id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            console.log(doc.data());
            vm.artist_id = doc.data().artist_id;
            vm.artist_name = doc.data().name;
            vm.artist_profile_pic = doc.data().profilePic;
          });
        });
      });
  },
  watch: {
    $route: 'fetchData'
  },
  // mounted() {
  //   this.getArtist();
  // },
  methods: {
    // async getArtist() {
    //   let snapshot = await db
    //     .collection('artists')
    //     .where('artist_id', '==', this.$route.params.id)
    //     .get();

    //   let artists = [];
    //   snapshot.forEach(doc => {
    //     let appData = doc.data();
    //     appData.id = doc.id;
    //     artists.push(appData);
    //   });

    //   this.artistData = artists[0];
    // }
    fetchData() {
      db.collection('artists')
        .where('artist_id', '==', this.$route.params.id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.artist_id = doc.data().artist_id;
            this.artist_name = doc.data().name;
            this.artist_profile_pic = doc.data().profilePic;
          });
        });
    }
  }
};
</script>

<style scoped></style>