import Vue from 'vue';
import Router from 'vue-router';
import Home from './components/Home.vue';
import Artist from './components/Artist.vue';
import NewArtist from './components/NewArtist.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/artist/:id',
      name: 'artist',
      component: Artist
    },
    {
      path: '/newArtist',
      name: 'newArtist',
      component: NewArtist
    }
  ]
});
